import $ajax from '@utils/ajax';
const serverUrl = $ajax.serverUrl;

const preAuth = '/auth-center';
const preUser = '/auth-user-center';

const roleServerUrl = `${preAuth}/role_hx/`;
const menuServerUrl = `${preAuth}/menu_hx/`;
const stationServerUrl = `${preUser}/station/`;
const dataAuthServerUrl = `${preAuth}/data-authority/`;
const userServerUrl = `${preUser}/user/`;

// 角色查询
export async function roleQuery(params) {
  return $ajax.post(serverUrl(`${roleServerUrl}query`), params);
}
// 添加角色
export async function roleAdd(data) {
  return $ajax.post(serverUrl(`${roleServerUrl}add`), data);
}
// 根据角色Id查询角色详细信息
export async function queryRoleDetail({ roleId }) {
  return $ajax.post(serverUrl(`${roleServerUrl}${roleId}/query/role`));
}
// 编辑角色
export async function roleEdit(data) {
  return $ajax.post(serverUrl(`${roleServerUrl}edit`), data);
}
// 删除角色
export async function roleDel(id) {
  return $ajax.get(serverUrl(`${roleServerUrl}${id}/del`));
}

// 查询指定岗位拥有的角色信息(1)
export async function roleGroup() {
  return $ajax.post(serverUrl(`${roleServerUrl}group`));
}
// 角色组查询
export async function roleGroupQuery(params, id) {
  return $ajax.get(serverUrl(`/auth-center/rolegroup/query`), params);
}
// 新增角色组
export async function rolegroupAdd(data) {
  return $ajax.post('/api/auth-center/rolegroup/add', data);
}
// 删除角色组
export async function rolegroupDel(roleGroupId) {
  return $ajax.get(serverUrl(`/auth-center/rolegroup/del/${roleGroupId}`));
}
// 新增角色
export async function roleAddV2(data) {
  return $ajax.post(serverUrl(`/auth-center/role/add/v2`), data);
}
// 编辑角色
export async function roleEditV2(data) {
  return $ajax.post(serverUrl(`/auth-center/role/edit/v2`), data);
}
// 角色组编辑
export async function rolegroupEditV1(data) {
  return $ajax.post(serverUrl(`/auth-center/rolegroup/edit`), data);
}
// 编辑菜单
export async function munuEditV1(id, data) {
  // return $ajax.post(`/api/auth-center/menu/${id}/edit`, data);
  return $ajax.post(`${menuServerUrl}${id}/edit?title=` + data);
}

